.EasyNavList {
  display: inline-block;
  list-style: none;
  padding: 0;
  margin: 0;
}
.EasyNavList * {
  display: inline-block;
  text-decoration: none;
  padding: 10px;
  font-size: 1.2em;
}
.EasyNavList li {
  padding: 0;
}
.EasyNavList li a p {
  padding: 0;
  margin: 0;
}
EasyNavList ul {
  list-style: none;
}



