@import url('https://fonts.googleapis.com/css?family=Dancing+Script');
@-webkit-keyframes bump {
  0% {text-shadow: 0px 0px 10px black;font-size: 2em;color:gray;}
  5% {-webkit-transform: scale(1.3);transform: scale(1.3);}
  25% {-webkit-transform: none;transform: none;}
  30% {font-size: 1.9em;-webkit-transform: rotateX(60deg);transform: rotateX(60deg);}
  50% {font-size: 2em;-webkit-transform: rotateZ(10deg);transform: rotateZ(10deg);color:white;}
  70% {text-shadow: 0px 0px 10px grey;-webkit-transform: rotateZ(-10deg);transform: rotateZ(-10deg)}
  100% {text-shadow: 0px 0px 10px black;-webkit-transform: none;transform: none;}
}
@keyframes bump {
  0% {text-shadow: 0px 0px 10px black;font-size: 2em;color:gray;}
  5% {-webkit-transform: scale(1.3);transform: scale(1.3);}
  25% {-webkit-transform: none;transform: none;}
  30% {font-size: 1.9em;-webkit-transform: rotateX(60deg);transform: rotateX(60deg);}
  50% {font-size: 2em;-webkit-transform: rotateZ(10deg);transform: rotateZ(10deg);color:white;}
  70% {text-shadow: 0px 0px 10px grey;-webkit-transform: rotateZ(-10deg);transform: rotateZ(-10deg)}
  100% {text-shadow: 0px 0px 10px black;-webkit-transform: none;transform: none;}
}

.sitenav {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  user-drag: none;
  cursor: default;
  -webkit-transition: opacity 0.5s ease-out;
  -o-transition: opacity 0.5s ease-out;
  transition: opacity 0.5s ease-out;
}


/*.sitenav nav {
  border-bottom: 10px solid #6e8b90;
}*/

.sitenav:hover {
  opacity: 1;
}


.sitenav nav {
  text-align: center;
  background-color: rgba(37, 33, 33, 0.7);
  text-shadow: 0px 0px 10px rgba(150, 150, 150, 1);
}

.sitenav .navlink {
  padding: 5px 30px 5px 30px;
  color: #c39898;
  background-color: #36383e;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
  margin: 20px 0 20px 0;
}

.sitenav .navlink:hover {
  -webkit-transition: background-color 0.5s ease, padding 0.2s ease;
  -o-transition: background-color 0.5s ease, padding 0.2s ease;
  transition: background-color 0.5s ease, padding 0.2s ease;
  text-shadow: none;
  /*transform: rotateX(30deg);*/
  background-color: #778094;
  padding: 10px 30px 10px 30px;
  margin: 0;
  border-radius: 5px;
  color: black;
}

.sitenav .gradient-pattern {
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
  border: none;
  font: normal 0.5px/normal Arial, Helvetica, sans-serif;
  color: rgba(255, 255, 255, 1);
  -o-text-overflow: clip;
  text-overflow: clip;
  background: -webkit-linear-gradient(135deg, rgba(0, 0, 0, 0) 25%, rgba(255, 255, 255, 0.2) 25%, rgba(255, 255, 255, 0.2) 50%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0) 75%, rgba(255, 255, 255, 0.2) 75%), -webkit-linear-gradient(45deg, rgba(0, 0, 0, 0) 25%, rgba(255, 255, 255, 0.2) 25%, rgba(255, 255, 255, 0.2) 50%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0) 75%, rgba(255, 255, 255, 0.2) 75%), rgb(0, 153, 151);
  background: -o-linear-gradient(135deg, rgba(0, 0, 0, 0) 25%, rgba(255, 255, 255, 0.2) 25%, rgba(255, 255, 255, 0.2) 50%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0) 75%, rgba(255, 255, 255, 0.2) 75%), -o-linear-gradient(45deg, rgba(0, 0, 0, 0) 25%, rgba(255, 255, 255, 0.2) 25%, rgba(255, 255, 255, 0.2) 50%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0) 75%, rgba(255, 255, 255, 0.2) 75%), rgb(0, 153, 151);
  background: linear-gradient(-45deg, rgba(0, 0, 0, 0) 25%, rgba(255, 255, 255, 0.2) 25%, rgba(255, 255, 255, 0.2) 50%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0) 75%, rgba(255, 255, 255, 0.2) 75%), linear-gradient(45deg, rgba(0, 0, 0, 0) 25%, rgba(255, 255, 255, 0.2) 25%, rgba(255, 255, 255, 0.2) 50%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0) 75%, rgba(255, 255, 255, 0.2) 75%), rgb(0, 153, 151);
  background-position: auto auto;
  background-origin: padding-box;
  background-clip: border-box;
  background-size: 200em 200em;
}

.sitenav .navborder {
  height: 10px;
  width: 100%;
  overflow-y: hidden;
  background-color: #524848;
}
.sitenav h1 {
  font-family: 'Dancing Script', cursive;
  margin: 0;
  vertical-align: middle;
  padding-bottom: 10px;
  font-size: 3em;
  color: white;
  -webkit-transition: color 0.3s ease, -webkit-transform 0.3s ease;
  transition: color 0.3s ease, -webkit-transform 0.3s ease;
  -o-transition: transform 0.3s ease, color 0.3s ease;
  transition: transform 0.3s ease, color 0.3s ease;
  transition: transform 0.3s ease, color 0.3s ease, -webkit-transform 0.3s ease;
  text-shadow: 0px 0px 10px black;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}
.sitenav .cycleicon {
  font-size: 0.6em;
  -webkit-filter: invert(75%) brightness(175%);
          filter: invert(75%) brightness(175%);
}
