.home {
  padding: 0;
  margin: 0;
  width: 100%;
  text-align: center;
}
.home * {
  clear: both;
}
.home h1, .home p {
  z-index: 2;
}
.home h1 {
  font-size: 2.5em;
  padding: 0;
  padding-top: 25px;
  margin: 0;
}
.home p {
  font-size: 2.2em;
}
.display * {
  display: inline-block;
  vertical-align: middle;
}
.display .medium {
  width: 20%;
  height: 20%;
  margin: 0;
  padding: 2.5%;
}
.display {
  position: fixed;
  bottom: 0;
  left: 0;
}
.display .big {
  width: 25%;
  height: 25%;
  margin: 0;
  padding: 3%;
}
.display .small {
  width: 15%;
  height: 15%;
  margin: 0;
  padding: 1.875%;

}
.display .java {
  -webkit-filter: invert(100%);
          filter: invert(100%);
}

@media (max-width: 900px) {
  .home p {
    font-size: 2em;
  }
}
@media (max-width: 750px) {
  .home p {
    font-size: 1.7em;
  }
}
@media (max-height: 500px) {
  .home p {
    font-size: 1.4em;
  }
}
@media (max-height: 600px) {

  .display .big {
    width: 12.5%;
    height: 12.5%;
    margin: 0;
    padding: 1.5%;
  }

  .display .small {
    width: 7.5%;
    height: 7.5%;
    margin: 0;
    padding: 0.9375%;
  }
}
@media (max-height: 700px) {
  .home p {
    font-size: 1.5em;
  }
}
@media (max-height: 500px) {
  .home h1 {
    font-size: 2em;
  }
}
@media (max-height: 400px) {
  .display {
    display: none;
  }
}
@media only screen and (max-device-width: 480px) {
  .home p {
    font-size: 4em;
  }
  .home h1 {
    font-size: 7em;
  }
}



