
.loginlink {
  -webkit-transition: color 0.5s linear,font-size 3s ease;
  -o-transition: color 0.5s linear,font-size 3s ease;
  transition: color 0.5s linear,font-size 3s ease;
  text-decoration: none;
  color: #bdbdc3;
  padding: 5px 10px 5px 10px;
}
.loginlink:hover {
  font-size: 1.05em;
  -webkit-transition: color 1s ease,font-size 3s ease;
  -o-transition: color 1s ease,font-size 3s ease;
  transition: color 1s ease,font-size 3s ease;
  color: #c5a3a3;
}



